const db = app.firestore();

function addNotes(id, notes1, notes2, notes3, notes4) {
  db.collection("notes").add({
    id,
    notes1,
    notes2,
    notes3,
    notes4,
  });
}

function setNotes(id, notes1, notes2, notes3, notes4) {
  db.collection("notes").doc("pyy5O4sYe1gP5rs7V2Ry").set({
    id,
    notes1,
    notes2,
    notes3,
    notes4,
  });
}

export { db, addNotes, setNotes };
